export default {
	data() {
		return {
			height: '',
			merchantId: '',
			allmoney: '',
			money: '',
			card: '',
			key: -1,
			xz: "../../static/user/xz.png",
			wxz: "../../static/user/wxz.png",
			type: 1,
		}
	},
	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'userinfo',
			success(e) {
				that.merchantId = e.data.id;
				that.getinfor();
			}
		});
	},
	onReady() {
		this.getTopheight();
	},
	methods: {
		getTopheight: function() {
			let that = this;
			uni.getSystemInfo({
				success(res) {
					that.height = res.statusBarHeight + 44 + "px";
				}
			})
		},
		getinfor: function() {
			let that = this;
			uni.request({
				url: this.serveipd + "/api/merchant/money/balance",
				method: 'GET',
				data: {
					merchantId: that.merchantId
				},
				success: res => {
					that.allmoney = res.data.data
				},
				fail: () => {},
				complete: () => {}
			});
			uni.request({
				url: this.serveipd + "/api/merchant/money/selectMerchantBank",
				method: 'GET',
				data: {
					merchantId: that.merchantId
				},
				success: res => {
					console.log(res);
					that.card = res.data.data
				},
				fail: () => {},
				complete: () => {}
			});
		},
		getAllmoney: function() {
			this.money = this.allmoney
		},
		choice: function(index) {
			this.key = index;
			this.type = 2;
		},
		zfb: function() {
			this.key = -1;
			this.type = 1;
		},
		extract: function() {
			let that = this;
			let num = /^[1-9]\d{0,7}(\.\d{1,3})?$|^0(\.\d{1,3})?$/
			if (that.money == '') {
				uni.showToast({
					title: '请输入金额',
					icon: "none"
				})
				return
			} else if (!num.test(that.money)) {
				uni.showToast({
					title: '金额输入有误',
					icon: "none"
				})
				return
			}
			let brokerage = that.money * 0.02;
			let bankCard = '';
			if (that.type == 1) {
				// 支付宝
				return
			} else {
				bankCard = that.card[that.key].bankCardno
			}
			if (parseInt(that.money) + brokerage <= that.allmoney) {
				uni.request({
					url: this.serveipd + "/api/merchant/money/merchantWithdrawal",
					header: {
						'Content-type': 'application/x-www-form-urlencoded'
					},
					method: 'POST',
					data: {
						merchantId: that.merchantId,
						money: that.money,
						handlingFee: brokerage,
						type: that.type,
						bankCard: bankCard
					},
					success: res => {
						if (res.data) {
							uni.showToast({
								title: '提现成功',
								duration: 2000,
								success() {
									setTimeout(function() {
										uni.navigateBack({
											 delta: 1
										});
									}, 2000)
								}
							});
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	}
}
